import { useContext, useEffect, useState } from 'react'
import Context from '../store/context'
import useGlobalState from '../store/useGlobalState';
import '../styles/Table.css'
import { Table } from 'antd'

const columns = [];
const subdata = {};
const data = []


const SingleTable = () => {
    const { state, actions } = useContext(Context);
    const [showTable, setShowingTable] = useState(false)

    useEffect(async () => {
        const Data = state.data[state.option_to_load]
        Object.keys(Data).map(x => { columns.push({ title: x, dataIndex: x.toLowerCase() }) })

        await columns.shift()

        await columns.map(x => {
            subdata[x.title] = Data[x.title]
        })

        await Object.assign(subdata, { key: '1' })
        await data.push(subdata)


        await console.log("columns => ", columns)
        await console.log(data)

        await setTimeout(() => setShowingTable(true), 1000)
    }, [input])



    return (
        <div className="table-container">
            {showTable ? <Table columns={columns} dataSource={data} /> : <div />}
        </div>
    )
}


export default SingleTable